import React from 'react';
import Slider from "react-slick";
import './filter.scss';

const FilterSlider = ({ sliderSettings, sliderData, additionalFilterWidth }) => {
  return (
    <div
      className="filter-slider"
      style={{
        width: additionalFilterWidth
          ? `calc( 100% - ${additionalFilterWidth} )`
          : '100%'
      }}
    >
      <Slider 
        dots={false}
        infinite={false}
        speed={300}
        variableWidth={true}
        swipe={true}
        {...sliderSettings}
        >
        {sliderData.length > 0 && sliderData.map((val) => {
          return (
            <div
              key={val.key}
              className="slide-content-container"
            >
              {val.slide}
            </div>
          )
        })}
      </Slider>
    </div>
  );
}

export default FilterSlider;
